import '/assets/less/main.less'

import Loading from '/components/loading.jsx'
const Load = loader => Loadable({
   loader,
   loading: Loading
})

const Page = Load(() => import('./app.jsx'))
const Home = Load(() => import('/page/home.jsx'))

