const routeConfig = (
	<Route path = {'/'} component = {Page}>
		<IndexRedirect to="/home/container"/>
		<Route path = {'home'} component = {Home}>
			<Route path = {'container'} component = {Container}/>
		</Route>
		<Route path = {'login'} component = {Login}/>
		<Route path = {'changePassword'} component = {ChangePassForm}/>

		<Route path = {'mgr'} component = {Mgr}/>
		<Route path = {'role'} component = {Role}/>
		<Route path = {'dept'} component = {Dept}/>

		<Route path = {'department'} component = {Department}/>
		<Route path = {'orgpost'} component = {Orgpost}/>

		<Route path = {'log'} component = {Log}/>
		<Route path = {'menu'} component = {Menu}/>
		<Route path = {'dict'} component = {Dict}/>
		<Route path = {'dictdata'} component = {DictData}/>
		<Route path = {'notice'} component = {Notice}/>
		<Route path = {'profile'} component = {Profile}/>

		<Route path = {'applymine'} component = {Apply}/>
		<Route path = {'applytodo'} component = {Apply}/>
		<Route path = {'applydone'} component = {Apply}/>

		<Route path = {'resumetemplate'} component = {ResumeTemplate}/>
		<Route path = {'project'} component = {Project}/>
		<Route path = {'workshop'} component = {Workshop}/>
		<Route path = {'worksystem'} component = {Worksystem}/>
		<Route path = {'equipment'} component = {Equipment}/>

		<Route path = {'plan'} component = {Plan}/>
		<Route path = {'zhengshu_add'} component = {Certificate}/>

		<Route path = {'zhengshu_sq'} component = {Plan}/>
		<Route path = {'projectIndex'} component = {ProjectIndex}/>

		<Route path = {'anli_add'} component = {CaseIndex}/>
		<Route path = {'shebao-import'} component = {Social}/>
		<Route path = {'userzheng-add'} component = {Paper}/>

		<Route path = {'workarea'} component = {ContractIndex}/>

	</Route>
)

ReactDom.render(
	<Provider store = { store }>
		<Router history = { hashHistory }>
        	{routeConfig}
		</Router>
	</Provider>
	,
    document.getElementById('root')
)


