import fetch from 'isomorphic-fetch'
import whitelist from '/api/whitelist'
import { message } from 'antd'
import { hashHistory } from 'react-router'

const handleRequest = (url, method, body = {}, json = false) => {
	let has = false
	whitelist.forEach(val => {
		if (val == url) has = true
	})

	let token = localStorage.getItem('token') || ''

	let header = Object.assign({}, {
		'Content-Type': json ? 'application/json' : 'application/x-www-form-urlencoded'
	}, has ? {} : {
		'Authorization': `Bearer ${token}`
	})

	let req = {
		method,
		headers: new Headers(header)
	}
	if (method == 'POST') {
		req = Object.assign({
			body: json ? JSON.stringify(body) : body,
			bodyUsed: true
		}, req)
	}

	// let wholeUrl = `${process.env.API_URL}${url}`
	let wholeUrl = url

	if (process.env.NODE_ENV == 'production') {
		wholeUrl = `/xpro${url}`
	}

	return new Request(wholeUrl, req)
}

const handleResponse = res => new Promise((rsl, rej) => {
	// if (res.status == 200) {
		rsl(res.json())
	// }
	// rej(res)
})
.then(res => {
	//
	if (res.code == 700) {
		message.warning('请先登录')
		window.LOGIN_LAST_PATH = hashHistory.getCurrentLocation().pathname
		hashHistory.push('/login')
	} else if (res.code != 200) {
		message.error(res.message)
	}
	//
	return res
})
.catch(err => {
	message.error('请求超时')
	console.error(new Error(`status: ${res.status}, statusText: ${res.statusText}`))
})

const handleTimeout = (url, type, body = {}, json = false, times = 10000) => Promise.race([
	new Promise((rsl, rej) => {
		setTimeout(_ => {
			rsl({status: 500, statusText: 'Timeout'})
		}, times)
	}),
	fetch(handleRequest(url, type, body, json))
])

const handleParams = (params, s = '') => {
	let str = ''
	for(let i in params){
		str += `${i}=${params[i]}&`
	}
	if (str.length) {
		str = `${s}${str.substring(0, str.length - 1)}`
	}
	return str
}



export default {
	tools: {
		handleParams
	},
	fetchGet (url, params = {}, times = 10000) {
		return handleTimeout(`${url}${handleParams(params, '?')}`, 'GET', null, times)
		.then(handleResponse)
	},
	fetchPost (url, params = {}, json = false) {
		return handleTimeout(url, 'POST', json ? params : handleParams(params), json)
		.then(handleResponse)
	},
	fetchBlob (url, params = {}) {
		return handleTimeout(`${url}${handleParams(params, '?')}`, 'GET').then(function(response) {
		  return response.blob();
		})
	}
}


